<template>
  <section id="OMC-Page">
    <div class="wrapper-container df dfv">
      <div class="query-form df">
        <el-form :inline="true" :model="queryForm" :label-width="'100px'" ref="queryForm" :rules="queryFormRules">

          <el-form-item :label="'局站名称'" prop="stationName">
            <el-input size="mini" v-model="queryForm.stationName"></el-input>
          </el-form-item>

          <el-form-item :label="'局站ID'" prop="stationId">
            <el-input size="mini" v-model="queryForm.stationId"></el-input>
          </el-form-item>

          <el-form-item :label="'FSUID'" prop="fsuId">
            <el-input size="mini" v-model="queryForm.fsuId"></el-input>
          </el-form-item>

          <el-form-item :label="'出厂码'" prop="serialNo">
            <el-input size="mini" v-model="queryForm.serialNo"></el-input>
          </el-form-item>

          <el-form-item :label="'局站状态'" prop="stationStatus">
            <el-select size="mini" v-model='queryForm.stationStatus'>
              <el-option v-for="item in [{id: null, name: '全部'}, {id: '在线', name: '在线'}, {id: '离线', name: '离线'}]" :key="item.name" :label="item.name" :value="item.id">{{item.name}}</el-option>
            </el-select>
          </el-form-item>

          <el-form-item :label="'引擎'">
            <el-form-item prop="engineVerStart">
              <el-input size="mini" v-model="queryForm.engineVerStart" style="width: 72.5px"></el-input>
            </el-form-item>
            <span> - </span>
            <el-form-item prop="engineVerEnd">
              <el-input size="mini" v-model="queryForm.engineVerEnd" style="width: 72.5px"></el-input>
            </el-form-item>
          </el-form-item>

          <el-form-item :label="'适配层'">
            <el-form-item prop="programVerStart">
              <el-input size="mini" v-model="queryForm.programVerStart" style="width: 72.5px"></el-input>
            </el-form-item>
            <span> - </span>
            <el-form-item prop="programVerEnd">
              <el-input size="mini" v-model="queryForm.programVerEnd" style="width: 72.5px"></el-input>
            </el-form-item>
          </el-form-item>

          <el-form-item :label="'SMonitor'">
            <el-form-item prop="smonitorVerStart">
              <el-input size="mini" v-model="queryForm.smonitorVerStart" style="width: 72.5px"></el-input>
            </el-form-item>
            <span> - </span>
            <el-form-item prop="smonitorVerEnd">
              <el-input size="mini" v-model="queryForm.smonitorVerEnd" style="width: 72.5px"></el-input>
            </el-form-item>
          </el-form-item>

          <el-form-item :label="' '" label-width="50px">
            <el-button type="primary" size="mini" @click="getList">查询</el-button>

            <el-upload
              class="upload-file no-file-list"
              style="display:inline-block;"
              ref="queryUpload"
              :on-success="queryUploadSuccess"
              :on-change="queryUploadChanged"
              :action="queryUploadUrl"
              :file-list="queryFileList">
              <el-button type="primary" size="mini">{{queryForm.fileName || '导入'}}</el-button>
            </el-upload>

            <el-button type="primary" size="mini" @click="reset">重置</el-button>
          </el-form-item>

        </el-form>
      </div>
      <div class="list df dfv f1">
        <div class="list-header">
          <h3>局站管理</h3>
          <div class="btn-group">
            <el-upload
              class="upload-file no-file-list"
              style="display:inline-block;"
              :on-success="SNUploadSuccess"
              ref="SNUpload"
              :action="SNUploadUrl"
              :file-list="SNFileList">
              <el-button type="primary" size="mini">导入</el-button>
            </el-upload>
            <el-button type='primary' size="mini" @click="exportTable">导出</el-button>
          </div>
        </div>
        <div class="table-box df dfv f1">
          <el-table :data="list" height="100%">
            <el-table-column prop="stationName" :label="'局站名称'"></el-table-column>
            <el-table-column prop="stationNo" :label="'局站ID'"></el-table-column>
            <el-table-column prop="fsuId" :label="'FSU ID'"></el-table-column>
            <el-table-column prop="serialNo" :label="'出厂码'"></el-table-column>
            <el-table-column prop="ip" :label="'IP'" :formatter="$tableFilter('nullFilter')"></el-table-column>
            <el-table-column prop="state" :label="'局站状态'" :formatter="$tableFilter('nullFilter')"></el-table-column>
            <el-table-column prop="offlineTime" :label="'离线时间'" :formatter="$tableFilter('dateFormatFilter')"></el-table-column>
            <el-table-column width="100" :label="'查看详情'">
              <template slot-scope="scope">
                <svg style="cursor: pointer;" @click="jump(scope.row)" width="16" height="16" xmlns="http://www.w3.org/2000/svg" xmlns:svg="http://www.w3.org/2000/svg">
                  <g>
                    <g id="Page-1" fill="none" fill-rule="evenodd">
                    <g fill="#00B1FF" id="svg_1">
                      <g id="svg_2">
                      <g id="svg_3">
                        <path d="m11.63636,0l-10.18181,0c-0.80291,0 -1.45455,0.65164 -1.45455,1.45455l0,13.0909c0,0.80291 0.65164,1.45455 1.45455,1.45455l10.18181,0c0.80291,0 1.45455,-0.65164 1.45455,-1.45455c0,-0.32072 -0.26036,-0.58181 -0.58182,-0.58181c-0.32145,0 -0.58182,0.26109 -0.58182,0.58181c0,0.16073 -0.13018,0.29091 -0.29091,0.29091l-10.18181,0c-0.16073,0 -0.29091,-0.13018 -0.29091,-0.29091l0,-13.0909c0,-0.16073 0.13018,-0.29091 0.29091,-0.29091l10.18181,0c0.16073,0 0.29091,0.13018 0.29091,0.29091l0,3.78181c0,0.32146 0.26037,0.58182 0.58182,0.58182c0.32146,0 0.58182,-0.26036 0.58182,-0.58182l0,-3.78181c0,-0.80291 -0.65164,-1.45455 -1.45455,-1.45455" id="Combined-Shape"/>
                        <path d="m2.78598,2.18182l4.18018,0c0.3327,0 0.60416,0.26765 0.60416,0.59568c0,0.32802 -0.27146,0.59567 -0.60416,0.59567l-4.18018,0c-0.3327,0 -0.60416,-0.26765 -0.60416,-0.59567c0,-0.32803 0.27146,-0.59568 0.60416,-0.59568z" id="Combined-Shape"/>
                        <path d="m2.18182,4.91844c0,-0.32802 0.27146,-0.59567 0.60416,-0.59567l7.32347,0c0.3327,0 0.60417,0.26765 0.60417,0.59567c0,0.32803 -0.27147,0.59568 -0.60417,0.59568l-7.32347,0c-0.3327,0 -0.60416,-0.26765 -0.60416,-0.59568z" id="Combined-Shape"/>
                      </g>
                      </g>
                    </g>
                    </g>
                    <ellipse stroke="#00B1FF" fill="none" stroke-width="1.2" cx="10.46856" cy="9.24605" id="svg_4" rx="2.47867" ry="2.47867"/>
                    <line fill="none" stroke-width="1.2" stroke-dasharray="null" stroke-linejoin="null" stroke-linecap="null" x1="11.6881" y1="11.17831" x2="13.81042" y2="13.64907" id="svg_6" stroke="#00B1FF"/>
                  </g>
                </svg>
              </template>
            </el-table-column>

          </el-table>

          <!-- Pagination -->
          <el-col :span="24" class="toolbar">
            <el-pagination
              layout="total, prev, pager, next,sizes, jumper"
              @current-change="pagination.handleCurrentChange"
              @size-change="pagination.pageSizeChange"
              :page-sizes="[10, 15, 50, 100]"
              :page-size="pagination.pageSize"
              :total="pagination.total"
              style="float:right;">
            </el-pagination>
          </el-col>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'OMC',
  data () {
    let purl = process.env.API_HOST;
    return {
      queryUploadUrl: purl + 'omc/importQueryStationFile',
      SNUploadUrl: purl + 'omc/importSNList',
      queryForm: {
        stationName: '', //局站名称
        stationId: '',   //StationNo
        fsuId: '',
        serialNo: '',    //出厂码
        stationStatus: null,   //局站状态。“在线”或“离线”
        engineVerStart: '', //引擎版本
        engineVerEnd: '',
        programVerStart: '',    //适配层版本
        programVerEnd: '',
        smonitorVerStart: '',   //SMonitor版本
        smonitorVerEnd: '',
        orderId: '',
        fileName: ''
      },
      list: [],
      pagination: {
        pageSize: 15,
        total: 0,
        page: 1,
        handleCurrentChange: (val) => {
          this.pagination.page = val;
          this.list = this.allList.slice((this.pagination.page - 1) * this.pagination.pageSize, this.pagination.page * this.pagination.pageSize)
        },
        pageSizeChange: (val) => {
          this.pagination.pageSize = val;
          this.list = this.allList.slice((this.pagination.page - 1) * this.pagination.pageSize, this.pagination.page * this.pagination.pageSize)
        }
      },
      queryFileList: [],
      SNFileList: [],
    }
  },
  computed: {
    queryFormRules () {
      const versionValidator = (rule, value, callback) => {
        if (!value) { callback(); return; }
        if (!/^([1-9]\d{0,}|\d)\.([1-9]\d{0,}|\d)\.([1-9]\d{0,}|\d)\.([1-9]\d{0,}|\d)$/.test(value)) {
          callback(new Error(' '))
        } else callback()
      }

      return {
        engineVerStart: {
          trigger: ['blur', 'change'], validator: versionValidator
        },
        engineVerEnd: {
          trigger: ['blur', 'change'], validator: versionValidator
        },
        programVerStart: {
          trigger: ['blur', 'change'], validator: versionValidator
        },
        programVerEnd: {
          trigger: ['blur', 'change'], validator: versionValidator
        },
        smonitorVerStart: {
          trigger: ['blur', 'change'], validator: versionValidator
        },
        smonitorVerEnd: {
          trigger: ['blur', 'change'], validator: versionValidator
        },
      }
    }
  },
  methods: {
    getList () {
      this.$refs.queryForm.validate(valid => {
        if (!valid) { return; }
        this.$api.omcQueryStationList({
          stationName: this.queryForm.stationName || undefined,
          stationId: this.queryForm.stationId || undefined,
          fsuId: this.queryForm.fsuId || undefined,
          serialNo: this.queryForm.serialNo || undefined,
          stationStatus: this.queryForm.stationStatus || undefined,
          engineVerStart: this.queryForm.engineVerStart || undefined,
          engineVerEnd: this.queryForm.engineVerEnd || undefined,
          programVerStart: this.queryForm.programVerStart || undefined,
          programVerEnd: this.queryForm.programVerEnd || undefined,
          smonitorVerStart: this.queryForm.smonitorVerStart || undefined,
          smonitorVerEnd: this.queryForm.smonitorVerEnd || undefined,
          orderId: this.queryForm.orderId || undefined,
        }).then(res => {
          this.allList = res.data;
          this.pagination.total = res.data.length;
          this.pagination.handleCurrentChange.call(this, 1);
        })
      })

    },
    reset () {
      this.$refs.queryForm.resetFields();
      this.queryForm.fileName = '';
      this.queryForm.orderId = undefined;
    },
    queryUploadSuccess (res) {
      this.$message({
        type: res.success ? 'success' : 'error',
        message: res.info
      })
      this.autoFileList = [];
      this.$refs.queryUpload.clearFiles();
      this.queryForm.orderId = res.data;
    },
    queryUploadChanged (file) {
      if (file.response && !file.response.success) {
        this.queryForm.fileName = ''
        this.$refs.queryUpload.clearFiles();
      } else {
        this.queryForm.fileName = file.name;
      }
    },
    SNUploadSuccess (res) {
      this.$message({
        type: res.success ? 'success' : 'error',
        message: res.info
      })
    },
    jump (row) {
      window.open("http://" + row.ip + ':2000', "_blank");
    },
    exportTable () {
      this.$refs.queryForm.validate(valid => {
        if (!valid) { return; }
        this.$api.omcExportStationList({
          stationName: this.queryForm.stationName || undefined,
          stationId: this.queryForm.stationId || undefined,
          fsuId: this.queryForm.fsuId || undefined,
          serialNo: this.queryForm.serialNo || undefined,
          stationStatus: this.queryForm.stationStatus || undefined,
          engineVerStart: this.queryForm.engineVerStart || undefined,
          engineVerEnd: this.queryForm.engineVerEnd || undefined,
          programVerStart: this.queryForm.programVerStart || undefined,
          programVerEnd: this.queryForm.programVerEnd || undefined,
          smonitorVerStart: this.queryForm.smonitorVerStart || undefined,
          smonitorVerEnd: this.queryForm.smonitorVerEnd || undefined,
          orderId: this.queryForm.orderId || undefined,
        }).then(res => {
          console.log(res.data);
          let fileName = '局站列表.xlsx';
          var blob = new Blob([res.data], {type: 'application/vnd.ms-excel'});
          var objectUrl = URL.createObjectURL(blob);
          var aEle = document.createElement("a");
          aEle.download = fileName;
          aEle.setAttribute("href", objectUrl);
          var event = document.createEvent("MouseEvent");
          event.initEvent("click", true, true);
          aEle.dispatchEvent(event);
        })
      })
    }
  },
  mounted () {
    this.allList = [];
    this.getList()
  }
}
</script>

<style lang="less" scoped>
  .list{
    margin-top: 10px;
    padding-bottom: 10px;
  }
</style>



